<template lang="html">
  <div class="">
    <navbar>
      <navbar-item :link="{name: 'root'}" name="Aceuill"></navbar-item>
      <navbar-item :link="{name: 'administration'}" name="Administration"></navbar-item>
      <div class="right menu">
        <div class="ui dropdown item">Admin<i class="dropdown icon"></i>
          <div class="menu">
            <a class="item">Editer</a>
            <a class="item" @click="logout">Se deconnecter</a>
          </div>
        </div>
    </navbar>
  </div>
</template>

<script>
import navbar from '../../Core/Navbar'
import navbarItem from '../../Core/NavbarItem'
var localStorage = require('web-storage')().localStorage
export default {
  data() {
    return {
    };
  },
  computed: {},
  ready() {},
  attached() {},
  methods: {
    logout(){
      localStorage.remove('authUser')
      this.$router.push({name: 'login'})
    }
  },
  components: {
    navbar,navbarItem
  },
  mounted: function (){
    $('.ui .dropdown').dropdown()
  }
};
</script>

<style lang="css">
</style>



