import VueRouter from 'vue-router'
var localStorage = require('web-storage')().localStorage


const router = new VueRouter({
  mode: 'hash',
  routes:[{
      name: 'root',
      path: '/',
      component: resolve => require(['./components/Global/Home.vue'],resolve)
  },{
    name: 'login',
    path: '/login',
    component: resolve => require(['./components/Global/Login.vue'],resolve)
  },{
      name: 'administration',
      path: '/administration',
      meta: {requiresAuth: true},
      component: resolve => require(['./components/Global/Auth/Administration.vue'],resolve),
      children: [{
          name: 'site.index',
          path: '/administration/site',
          meta: {requiresAuth: true},
          component: resolve => require(['./components/Global/Auth/Site/IndexSite.vue'],resolve),
        },{
            name: 'site.add',
            path: '/administration/site/add',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Site/AddSite.vue'],resolve),
        },{
            name: 'site.show',
            path: '/administration/site/:id(\\d+)',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Site/ShowSite.vue'],resolve),
        },{
            name: 'puit.add',
            path: '/administration/site/:id(\\d+)/puit/add',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Puit/AddPuit.vue'],resolve),
        },{
            name: 'puit.show',
            path: '/administration/site/:id(\\d+)/puit/:idpuit(\\d+)',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Puit/ShowPuit.vue'],resolve),
        }
      ]
  },{
      path: '*',
      redirect: '/'
  }]
})
router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    const authUser = localStorage.get('authUser')
    if (authUser && authUser.access_token) {
      next()
    }else{
      next({name: 'login'})
    }
  }else{
    next()
  }
})
export default router



